<template>
  <div id="movieplay">

    <div>
      <div>{{item.name}}</div>
      <video id="myVideo" autoplay controls preload="auto">
          <source :src="url"  type="application/x-mpegURL">
      </video>
    </div>

    <div>
      <div>{{item.name}}</div>
      <div>时间：{{item.time}}</div>
      <div>主演：{{item.author}}</div>
      <div>概述：{{item.introduction}}</div>
    </div>

    <div @click="$router.back(-1)">返回</div>

  </div>
</template>

<script>
  export default{
    data(){
      return{
        url:'',
        item:[],
        list:[]
      }
    },
    mounted() {
      this.url = this.$route.query.item.url
      this.item = this.$route.query.item
    }
  }
</script>

<style>
  #movieplay{
    width: 100vw;
    height: 100vh;
    background-color: #E1E8FF99;
  }
  #movieplay>div:first-child{
    width: 100%;
    height: 50vh;
  }
  #movieplay>div:first-child>div{
    height: 100px;
    line-height: 100px;
    font-weight: bolder;
    text-align: center;
    font-size: 1.25rem;
    color: #333;
  }
  #movieplay video{
    width: 100%;
    height: 300px;
    background-color: #E1E8FF99;
  }
  #movieplay>div:nth-of-type(2){
    width: 90%;
    margin: 60px 0 0 5%;
    color: gray;
  }
  #movieplay>div:nth-of-type(2)>div{
    margin: 10px 0;
  }
  #movieplay>div:nth-of-type(3){
    border: 0.5px solid;
    font-size: 0.875rem;
    position: absolute;
    bottom: 30px;
    width: 80px;
    height: 35px;
    border-radius: 18px;
    text-align: center;
    line-height: 35px;
    left: calc(50vw - 40px);
    color: gray;
  }
</style>



