<template>
  <div id="mlists">

    <div class="musiclist">
      <div v-for="item in list">
        <div>
          <img v-lazy="item.imgUrl">
          <div><img src="../../assets/playRight.png"><span>{{item.playnumber}}</span></div>
        </div>
        <div>{{item.title}}</div>
      </div>
    </div>

  </div>
</template>

<script>

  import err from "../../assets/error.png"
  import loading from "../../assets/loading.png"

  export default{

    data(){
      return{
        errorImg: 'this.src="'+err+'"',
        list:[]
      }
    },

    mounted() {
      this.getlist()
    },

    methods:{
      async getlist(){
        await axios({
          url: this.$url + "static/musiclists/" + "tuijian.json"
        })
        .then((data)=>{
          let list = data.data.list
          list.forEach((item)=>{
            item.imgUrl = this.$url + "static/images/" + item.imgUrl
          })
          this.list = data.data.list
        })
      }
    },

    // directives:{
    //   lazy:{
    //     bind:function(el){
    //       el.src = loading
    //     },
    //     inserted:function(el,binding){
    //       let x = setInterval(()=>{
    //           if( el.getBoundingClientRect().top < window.outerHeight ){
    //             el.src = binding.value
    //           }
    //       },1000)
    //     }
    //   }
    // }

  }

</script>

<style>
  #musiclist .musiclist{
    display: flex;
    flex-wrap: wrap;
    width: 96%;
    margin-left: 2%;
  }
  #musiclist .musiclist>div{
    width: 6.75rem;
    height: 9.2rem;
    margin: 0.625rem 0 0 0.625rem;
  }
  #musiclist .musiclist>div>div:first-child{
    width: 6.75rem;
    height: 6.75rem;
    overflow: hidden;
    border-radius: 8px;
  }
  #musiclist .musiclist>div>div:first-child>img{
    width: 100%;
    height: 100%;
  }
  #musiclist .musiclist>div>div:first-child>div{
    margin: -1.875rem 0 0 5px;
    display: flex;
    align-items: center;
    font-size: 0.875rem;
    color: #fff
  }
  #musiclist .musiclist>div>div:first-child>div>img{
    width: 0.8rem;
    height: 0.8rem;
    margin-right: 5px;
  }
  #musiclist .musiclist>div>div:last-child{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    font-size: 0.75rem;
    width: 90%;
    margin: 8px auto 0;
  }
</style>



