<template>
  <div>
      <!-- Head -->
     <div class="top">
      	<span>歌单</span>
      	<img @click="showOther=!showOther" src="../assets/set.png">
      	<div v-show="showOther" class="other">
            <router-link to="/">经典版</router-link>
      	</div>
      </div>

      <!-- 音乐列表 -->
      <div class="musicList">
      	<div class="musicItem" v-for='(item,index) in musicList' @touchstart="playthisX(index)" @touchmove="playthisM(index)" @touchend="playthis(item,index)">
      			<div class="name">
      				<div>{{item.name}}</div>
      				<div>
      					<span v-show="(item.sq) ? sqTrue : sqFalse" class="sq">SQ</span>
      					<span v-show="(item.alone) ? sqTrue : sqFalse" class="alone">独家</span>
      					<span class="author">{{item.author}}</span>
      				</div>
      			</div>
      			<div @touchstart.stop="touchsets" @touchend.stop="touchsete(item,index)" class="set">
      					<img src="../assets/set.png">
      			</div>
      	</div>
      </div>

      <!-- 底部 -->
      <div class="tabbar" @click="showPage">
      	<div :class="playing ? 'picture' : 'pictureS'"><img :src="item.imgUrl"></div>
      	<div class="nameAndAuthor">
      		<div>{{item.name}}</div>
      		<div>{{item.author}}</div>
      	</div>
      	<div v-show='!playing' class="play"><img @click.stop="playMusic" src="../assets/play.png"></div>
      	<div v-show='playing' class="play"><img @click.stop="playMusicStop" src="../assets/stop.png"></div>
      </div>


      <!-- 底部设置 -->
      <div v-show="setPageX" class="setPage">
      	<div class="setPageTop" @click="setPageDowDis"></div>
      	<div class="setPageDow">
      		<div class="picture">
      			<img :src="setImg">
      			<div class="setMsg">
      				<div>{{setName}}</div>
      				<div>{{setAuthor}}</div>
      			</div>
      		</div>
      		<div class="message">歌手：{{setAuthor}}</div>
      		<div class="message" @click="downloadMusic">下载</div>
      		<div class="message" @click="delMusic">删除</div>
      	</div>
      </div>

      <!-- 播放页 -->
      <div v-show="playingPage" class="playPage">
      	<!-- 顶部作者 -->
      	<div class="playPageTop">
      		<img @click="display" src="../assets/down.png">
      		<div>{{playPagename}}</div>
      		<div>{{playPageAuthor}}</div>
      	</div>
      	<!-- 旋转图片 -->
      	<div class="middle">
      		<div :class="playing ? 'picture' : 'pictureX'">
      			<div><img :src='playingImg'></div>
      		</div>
      	</div>
      	<!-- 播放条 -->
      	<div class="playLine">
      		<div class="time" v-text="oldPlayTime"></div>
      		<div class="timeLine">
      			<div class="oldLine"></div>
      			<div class="timeBall" @touchstart='changeByHandS' @touchmove='changeByHandM' @touchend='changeByHandE'></div>
      		</div>
      		<div class="time" v-text="totleTime"></div>
      	</div>
      	<!-- 播放/停止/下一首/前一首 -->
      	<div class="buttons">
      		<div @click="preMusic"><img src='../assets/playPagePlay.png'></div>
      		<div  @click.stop="playMusic"  v-show='!playing'><img src='../assets/playPagePlay.png'></div>
      		<div  @click.stop="playMusicStop"  v-show='playing'><img src='../assets/playPageStop.png'></div>
      		<div @click="nextMusic"><img src='../assets/playPagePlay.png'></div>
      	</div>
      </div>
  </div>
</template>

<style src="../css/music.css"></style>
<style src="../css/playPage.css"></style>
<style src="../css/setPage.css"></style>

<script>

	export default {
		data(){
      return{
        musicList: [],
        sqFalse: false,
        sqTrue: true,
        playingPage: false,
        setPageX: false,
        playPagename: "",
        playPageAuthor: "",
        playing: false,
        audioPlay: '',
        audioPlayTime: 0,
        setImg: "",//设置中图片
        setName: "",//设置中歌名
        setAuthor: "",//设置中作者
        downloadIndex: 0,//下载项对应的下标
        moveDistanceS: 0,//点击音乐滑动开始距离
        moveDistanceE: 0,//点击音乐滑动结束距离
        oldPlayTime: "0 : 00",//开始时间
        totleTime: "0 : 00",//结束时间
        totleTimeN: 0,
        timeOver: 0,//进度显示定时器
        entTimeOver: 0,
        handS: 0,
        handE: 0,
        numberE: 0,
        isplaying: 0,
        musicListIndex: 0,
        showOther: false,
        touchMusicLS: 0,//点击列表开始
        item: {
        	imgUrl: "",
        	imgUrl: "",
        	author: ""
        },
        playingImg: '',
      }
		},
		mounted(){
			this.audios()

      console.log(document.getElementsByClassName("xue"))

		},
		methods:{
			// 请求json数据
			async audios(){
				await axios({
					url: this.$url + 'static/homemini.json',
				})
				.then((data)=>{
					this.musicList = data.data.list
					this.item = data.data.list[0]
				})
			},

			//播放页面数据
			theTimes(){
				let timeAudio = document.createElement("audio")
				timeAudio.src = this.item.url
				var _this = this
				this.entTimeOver = setInterval(()=>{
					let x = (parseInt(timeAudio.duration) > 0) ? parseInt(timeAudio.duration) : 0
					_this.totleTimeN = x
					let xMin = parseInt(x/60)
					let xSec = parseInt(x%60).toString().padStart(2,"0")
					_this.totleTime = xMin + " : " + xSec//结束时间
					if( timeAudio.duration > 0 ){
						_this.clearEndTime()
					}
				},50)
			},
			//清除请求结束时间
			clearEndTime(){
				clearInterval(this.entTimeOver)
				this.entTimeOver = 0
			},

			//进度条
			showTimeLime(redLine){
				let x = document.getElementsByClassName("oldLine")[0]
				x.style.width = redLine
				let y = document.getElementsByClassName("timeBall")[0]
				y.style.left = redLine
			},
			//进度时时改变
			changeByTime(){
				var _this = this
				this.timeOver = setInterval(()=>{
					let st = this.audioPlay.currentTime
					let sMin = parseInt(st/60)
					let sSec = parseInt(st%60).toString().padStart(2,"0")
					this.oldPlayTime = sMin + " : " + sSec//开始时间
					let ss = (st/this.totleTimeN).toFixed(2)
					let redLine = ss*100 + "%"
					_this.showTimeLime(redLine)

					if( ss == 1 ){
						ss = 0
						let redLine = ss*100 + "%"
						_this.showTimeLime(redLine)
						this.nextMusic()
					}

				},1000)
			},

			//手动开始
			changeByHandS(e){
				this.handS = event.touches[0].clientX
			},
			//手动改变进度
			changeByHandM(e){
				clearInterval(this.timeOver)
				this.handE = event.touches[0].clientX
				let dis = this.handE - this.handS
				let widthTotle = document.getElementsByClassName("timeLine")[0].clientWidth
				dis = ( dis > widthTotle ) ? widthTotle : dis
				let number = (dis/widthTotle + (this.audioPlay.currentTime/this.totleTimeN)).toFixed(2)
				number = ( number > 1 ) ? 1 : number
				this.numberE = number
				let numberS = number*100 + "%"
				if( this.entTimeOver == 0 ){
					this.showTimeLime(numberS)
				}
			},
			//手动结束
			changeByHandE(e){
				this.audioPlay.currentTime = this.totleTimeN*this.numberE
				this.changeByTime()
			},

			// 播放页面
			showPage(){
				this.playingPage = true
				this.playingImg = this.item.imgUrl
				this.playPagename = this.item.name
				this.playPageAuthor = this.item.author
				var _this = this
				setTimeout(()=>{
					document.getElementsByClassName("playPage")[0].style.top = 0
					document.getElementsByClassName("playPage")[0].style.opacity = 1
					this.theTimes()
					if( this.isplaying == 1 ){
						this.changeByTime()
					}
				},10)
			},

			// 关闭播放页面
			display(){
				document.getElementsByClassName("playPage")[0].style.top = '30vh'
				document.getElementsByClassName("playPage")[0].style.opacity = 0
				setTimeout(()=>{
					this.clearEndTime()
					this.playingPage = false
				},500)
			},

			// 点击音乐
			playthisX(index){
				document.getElementsByClassName("musicItem")[index].style.backgroundColor = "#DEDEDE"
				this.moveDistanceS = event.touches[0].clientY
			},
			playthisM(index){
				document.getElementsByClassName("musicItem")[index].style.backgroundColor = "transparent"
				this.moveDistanceE = event.touches[0].clientY
			},
			playthis(item,index){

				this.musicListIndex = index

				clearInterval(this.timeOver)
				this.clearEndTime()

				document.getElementsByClassName("musicItem")[index].style.backgroundColor = "transparent"

				//判断滑动距离，判断是否在滑动
				if( this.moveDistanceE > 0 ){
					let x = this.moveDistanceS - this.moveDistanceE
					this.moveDistanceE = 0//清除滑动距离
					if( x > 10 || x < -10 ){return}
				}

				this.item = item
				if( this.audioPlay !=  "" ){
					this.audioPlay.pause();
					this.audioPlay = ""
				}
				this.audioPlayTime = 0
				this.playing = true
				this.audioPlay = document.createElement("audio");
				this.audioPlay.src = this.item.url;
				this.audioPlay.play();
				this.audioPlay.currentTime = this.audioPlayTime

				this.changeByTime()
			},

			// 点击设置
			touchsets(e){
				event.target.style.backgroundColor = "#DEDEDE"
			},
			touchsete(item,index){
				event.target.style.backgroundColor = "transparent"
				this.downloadIndex = index
				setTimeout(()=>{
					this.setPageX = true
					this.setImg = item.imgUrl
					this.setName = item.name
					this.setAuthor = item.author
				},10)
			},
			setPageDowDis(){
				this.setPageX = false
			},

			// 点击播放按钮
			playMusic(){
				this.isplaying = 1
				this.playing = (this.playing == false) ? true : false
				this.audioPlay = document.createElement("audio");
				this.audioPlay.src = this.item.url;
				this.audioPlay.play();
				this.audioPlay.currentTime = this.audioPlayTime
				this.changeByTime()
			},

			// 点击停止播放按钮
			playMusicStop(){
				this.isplaying = 0
				this.playing = (this.playing == false) ? true : false,
				this.audioPlayTime = this.audioPlay.currentTime
				this.audioPlay.pause();
				clearInterval(this.timeOver)
				this.clearEndTime()
			},

			//前一首
			preMusic(){
				this.musicListIndex--
				let x = this.musicList.length - 1
				this.musicListIndex = (this.musicListIndex == -1) ? x : this.musicListIndex
				let redLine = 0
				this.showTimeLime(redLine)
				this.playNextMusic()
			},

			//后一首
			nextMusic(){
				this.musicListIndex++
				let x = this.musicList.length
				this.musicListIndex = ( this.musicListIndex == x ) ? 0 : this.musicListIndex
				let redLine = 0
				this.showTimeLime(redLine)
				this.playNextMusic()
			},

			//点击下一首或者前一首是切换歌曲
			playNextMusic(){
				clearInterval(this.timeOver)
				this.clearEndTime()
				this.item = this.musicList[this.musicListIndex]
				this.playthis(this.item,this.musicListIndex)
				this.showPage()
			},


			// 下载音频
			downloadMusic(){
				let a = document.createElement("a")
				a.href = this.musicList[this.downloadIndex].url
				a.download = this.musicList[this.downloadIndex].name
				let ev = new MouseEvent("click")
				a.dispatchEvent(ev)
			},



			// 删除音频
			delMusic(){
				this.setPageX = false
				this.musicList.splice(this.downloadIndex,1)
				this.audioPlayTime = 0
				this.audioPlay.pause();
				this.item = this.musicList[0]
			}
		}
	}

</script>



