<template>
  <div class="videos">

    <!-- //视频列表 -->
     <div v-for="(item,index) in videolist">

          <div class="video">
            <video
              controls="controls"
              :poster="item.imgUrl"
              :src="item.videoUrl"></video>
          </div>
          <div class="videoname">{{item.title}}</div>
          <div class="msg">
              <div class="authorImg"><img :src="item.authorImg"></div>
              <span>{{item.author}}</span>
              <div class="number">
                <img src="../../assets/like.png">
                <span>{{item.likeNumber}}</span>
                <img src="../../assets/messageblue.png">
                <span>{{item.discussion}}</span>
                <img src="../../assets/set.png">
              </div>
          </div>

      </div>

      <!-- 暂无视频 -->
      <nothing v-if="nothingtip"></nothing>



  </div>
</template>

<script>

  import nothing from "./nothing.vue"
  export default{
    props:["url","page"],

    data(){
      return{
        playindex: 0,
        videolist:[],
        nothingtip:false,
        nowpage:0
      }
    },
    components:{"nothing":nothing},
    mounted() {
     this.init()
    },
    methods:{
      //判断是否为 首次 进入页面
      init(){
        if( this.url != "" ){
          this.getvideos()
        }
      },

      //请求视频
      async getvideos(){
        await axios({
          url: this.$url + "static/videojson/" + this.url
        })
        .then((data)=>{
            let list = data.data.list
            list.forEach((item)=>{
              item.authorImg = this.$url + item.authorImg
            })
            this.videolist = list
            this.nothingtip = ( this.videolist.length == 0 ) ? true : false
        })
      },



    }
  }

</script>



