<template>
  <div id="musiclist" @touchmove='pagemove'>

    <div>歌单</div>

    <personmusic></personmusic>

    <div class="sorts">
      <router-link to="/movies" tag="div">
        <img src="../assets/musicalbums.png">视频</router-link>
      <div><img src="../assets/musiclist.png">歌单</div>
      <div><img src="../assets/ranking.png">排行榜</div>
      <div @click="showmore"><img class="moreImg" :style="morechange" src="../assets/down-f.png"><span v-text="more"></span></div>
    </div>

    <moresort></moresort>


    <div class="mainlist" :style="updown">

        <div class="musicsort"><div>
            <div v-for="(text,index) in sortlist"
              @click="showthislist(index)"
              :key="index">{{text}}</div>
        </div></div>

        <div class="lists" ><div :style="thewidth">
          <musiclists
          v-for="(count,index) in sortlist.length"
          :key="index"></musiclists>
        </div></div>

    </div>

  </div>
</template>

<style src="../css/musiclist.css"></style>

<script>

  import personmusic from "../components/music/personmusic.vue"
  import moresort from "../components/musiclist/moresort.vue"
  import musiclists from "../components/musiclist/musiclists.vue"

  export default{
    data(){
      return{
        more:"更多",
        morechange:{transform: "rotate(0deg)"},
        updown:{marginTop:"-180px"},
        sortlist:[],
        showlist:["true"],
        thewidth:{width:"0px"},
        upmovepoint:0,//滑动移动点
      }
    },
    components:{
      'personmusic':personmusic,
      'moresort':moresort,
      "musiclists":musiclists
    },
    mounted() {
      this.getsort()
      this.makeshowlist()
    },
    methods:{

      //点击更多，收起或者展开
      showmore(){
        this.more = (this.more == "更多") ? "收起" : "更多"
        let x = "rotate(-180deg)"
        let y = "rotate(0deg)"
        this.morechange.transform =  (this.morechange.transform == y) ? x : y
        let a = "-180px"
        let b = "0px"
        this.updown.marginTop = (this.updown.marginTop == a) ? b : a
      },

      //获取歌单分类
      async getsort(){
        await axios({
          url: this.$url + "static/musiclists/sort.json"
        })
        .then((data)=>{
          this.sortlist = data.data.sorts
        })
      },

      //进入创建显示列表
      makeshowlist(){
        let _this = this
        let make = setInterval(()=>{
          if( _this.sortlist.length > 0 ){
            _this.thewidth.width = window.outerWidth*_this.sortlist.length +"px"
            for(let a=0;a<9;a++){
              _this.showlist.push("")
            }
            _this.showlist[0] = "true"
            clearInterval(make)
          }
        },300)
      },

      //点击显示
      showthislist(index){
        this.showlist[index] = "true"
      },

      //页面滑动
      pagemove(){
      }


    }
  }

</script>



