<template>
  <div class="hometop">
    <div class="title">
      <span @click="changet(1)" :class="change(1)">推荐</span
      ><span @click="changet(2)" :class="change(2)">歌手</span
      ><span @click="changet(3)" :class="change(3)">本地</span>
    </div>
    <div :class="changeline()"></div>
    
  </div>
</template>

<script>

  export default{
	  props:["listindex"],
    data(){
      return{
        line: "linem",
      }
    },
    mounted() {
    },
    methods:{
      
      changet(x){this.$emit('autofunction',x)},
      
      changeline(){
        switch(this.listindex){
          case 1:{return "linem"};
          case 2:{return "linea"};
          case 3:{return "linev"}
        }
      },
      
      change(x){
        if( x == this.listindex ){
          return "choosed"
        }
      }

    }
  }

</script>



