<template>
  <div class="recomList">

      <div class="itemList" v-for="(item,index) in list" :key="index" @click="playmusic(item)">
          <div class="left">
            <div>{{item.name}}</div>
            <div><span class="sq" v-if="item.sq==true">SQ</span
                ><span class="alone" v-if="item.alone"
                >独家</span>{{item.author}}
            </div>
          </div>
          <img @click.stop="showshare(item)"  src="../../assets/set.png">
      </div>

  </div>
</template>

<script>
  export default{
    data(){
      return{
        list:[]
      }
    },
    mounted() {
      this.getList()
      this.touchcolor()
    },
    methods:{

        //获取音乐列表
        getList(){
          axios({
            url: this.$url + "static/homemini.json"
          })
          .then((data)=>{
            let list = data.data.list
            list.forEach((item)=>{
              item.url = "https://alt012577.github.io/themusics/" + item.url
            })
            this.list = list
            this.$audio.src = this.list[0].url
            this.getendtime()
          })
        },

        //播放音乐
        playmusic(item){
          this.$audio.src = item.url
          this.$audio.play()
          this.getendtime()
          this.$root.playing = true
          let anim = document.getElementsByClassName("playbottomImg")[0].style
          anim.animationPlayState = "running"
        },

        //获取歌曲长度
        getendtime(){
          let duration = setInterval(()=>{
            if( this.$audio.duration > 0 ){
              this.$root.numberend = this.changenumber(this.$audio.duration)
              clearInterval(duration)
            }
          },10)
        },

        //更改数字样式
        changenumber(x){
          let number = parseInt(x)
          let first = parseInt(number/60).toString().padStart(2,"0")
          let second = parseInt(number%60).toString().padStart(2,"0")
          let y = first + " : " + second
          return y
        },

        //添加点击样式
        touchcolor(){
          setTimeout(()=>{
            let other = document.getElementsByClassName("recomList")[0].children
            let others = [].slice.call(other)
            others.forEach((item)=>{
              item.addEventListener("touchstart",function(){
                item.style.backgroundColor = "#f1f1f1"
              })
              item.addEventListener("touchend",function(){
                item.style.backgroundColor = "transparent"
              })
            })
          },100)

        },


        //显示分享页面
        showshare(x){
          this.$emit("share",x)
        },
        showshares(){}


    }
  }
</script>



