import Vue from 'vue'
import Router from 'vue-router'
import index from '@/page/index'
import alt012577 from '@/page/alt012577'
import HomeMini from '@/page/HomeMini'
import set from '@/page/set'
import backgammon from '@/page/backgammon'
import movies from '@/page/movies/movies'
import movieplay from '@/page/movies/movieplay'
import tv from '@/page/movies/tv'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/alt012577',
      name: 'alt012577',
      component: alt012577,
      meta:{keepAlive: true,}
    },
    {
      path: '/HomeMini',
      name: 'HomeMini',
      component: HomeMini
    },
    {
      path: '/set',
      name: 'set',
      component: set
    },
    {
      path: '/backgammon',
      name: 'backgammon',
      component: backgammon
    },
    {
      path: '/movies',
      name: 'movies',
      component: movies
    },
    {
      path: '/movieplay',
      name: 'movieplay',
      component: movieplay
    },
    {
      path: '/tv',
      name: 'tv',
      component: tv
    }
  ]
})



