<template>
  <div id="movies">

    <div class="top">
      <router-link to="/alt012577">
        <img src="../../assets/backfff.png">
      </router-link>
      <div @click="changepage(0)" :class="choosed(0)">电影</div>
      <div @click="changepage(1)" :class="choosed(1)">电视剧</div>
      <img src="../../assets/searchfff.png">
    </div>

    <div class="search"><input @input="chooselist" v-model="searchwords" /><span>搜索</span></div>

    <div class="list" v-show="pageshow(0)">
        <div @click="playmovie(item)" v-for="item in movielist">
          <div><img :src="item.img"></div>
          <div>
            <div>{{item.name}}</div>
            <div>时间：{{item.time}}</div>
            <div>演员：{{item.author}}</div>
            <div>概述：{{item.introduction}}</div>
          </div>
        </div>
    </div>

    <div v-if="tv" v-show="pageshow(1)" class="list">
        <div @click="playtv(item)" v-for="item in tvlist">
          <div><img :src="item.img"></div>
          <div>
            <div>{{item.name}}</div>
            <div>时间：{{item.time}}</div>
            <div>演员：{{item.author}}</div>
            <div>概述：{{item.introduction}}</div>
          </div>
        </div>
    </div>



  </div>
</template>
<script>
  export default{
    data(){
      return{
        page:0,
        searchwords:"",
        movielist:[],
        movieList:[],
        tvlist:[],
        touch:0,
        tv:false
      }
    },
    mounted() {
      this.getmovies()
      this.choosed()
    },
    methods:{

      //获得电影数据
      async getmovies(){
        await axios({
          url:this.$url + "static/movies/movies.json"
        })
        .then((data)=>{
          this.movieList = data.data.list
          this.movielist = this.movieList
        })
      },

      //获取 tv 列表
      async gettv(){
        await axios({
          url:this.$url + "static/movies/tv.json"
        })
        .then((data)=>{
          this.tvlist = data.data.list
        })
      },

      //页面颜色
      choosed(x){
        if(this.page == x){return "choosed"}
      },

      //切换页面
      changepage(x){
        this.page = x
        if( this.touch == 0 ){
          this.touch = 1
          this.gettv()
          this.tv=true
        }
      },

      //显示页面
      pageshow(x){
        if(this.page == x){return true}
      },


      //跳转电影播放页面
      playmovie(item){
        this.$router.push({path:'/movieplay',query:{item:item}})
      },

      //跳转 tv 播放页面
      playtv(item){
        this.$router.push({path:'/tv',query:{item:item}})
      },

      //挑选视频
      chooselist(){
        if( this.searchwords.length > 0 ){
          this.movielist = []
          this.movieList.forEach((item)=>{
            let x = item.name.indexOf(this.searchwords)
            if( x > -1 ){
              this.movielist.push(item)
            }
          })
        }else{
          this.movielist = this.movieList
        }
      }


    }
  }
</script>

<style>
  #movies{
    width: 100vw;
    height: 100vh;
    overflow: scroll;
    background-color: #E1E8FF99;
  }
  #movies .top{
    position: fixed;
    width: 100%;
    height: 40px;
    text-align: center;
    line-height: 50px;
    display: flex;
    align-items: center;
    color: #fff;
    padding-top: 20px;
    background-image: linear-gradient(to top,#3B62E3,#536BBD);
  }
  #movies .top a{
    width: 20px;
    height: 20px;
    padding: 10px 20px;
    display: flex;
  }
  #movies .top img{
    width: 20px;
    height: 20px;
    /* padding: 10px 20pdx; */
  }
  #movies .top div{
    width: calc(50vw - 50px);
  }
  #movies .choosed{
    font-weight: bolder;
    color: #D8C38F;
  }
  #movies .search{
    margin-top: 60px;
    width: 100vw;
    height: 80px;
    background-color: #E1E8FF55;
    display: flex;
    align-items: center;
  }
  #movies .search input{
    width: 70%;
    font-size: 16px;
    height: 40px;
    border-radius: 8px;
    border: none;
    margin: 0 0 0 20px;
    outline: none;
  }
  #movies .search span{
    display: block;
    height: 36px;
    border-radius: 8px;
    background-color: #fff;
    margin-left: 20px;
    width: 50px;
    text-align: center;
    line-height: 36px;
  }
  #movies .list{
    width: 100%;
  }
  #movies .list>div{
    width: 100%;
    display: flex;
  }
  #movies .list>div>div:first-child{
    width: 30%;
    height: 11rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  #movies .list>div>div:last-child{
    width: 70%;
    font-size: 0.8rem;
    height: 11rem;
    overflow: hidden;
  }
  #movies .list img{
    border-radius: 5px;
    width: 90%;
    height: 90%;
  }
  #movies .list>div>div:last-child div:nth-of-type(1){
    font-weight: bolder;
    margin: 0.5rem 0;
    font-size: 1rem;
  }
  #movies .list>div>div:last-child div:nth-of-type(2){
    color: gray;
    margin-bottom: 0.5rem;
  }
  #movies .list>div>div:last-child div:nth-of-type(3){
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    margin-bottom: 0.5rem;
  }
  #movies .list>div>div:last-child div:nth-of-type(4){
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }
</style>



