<template>
  <div id="Home">

    <!-- 顶部导航 -->
    <top @autofunction="autochange" v-bind:listindex="listindex"></top>

    <!-- 页面 -->
    <div class="homepages">
      <div class="homemiddle"

        @touchstart="midmoves"
        @touchmove="midmoveing"
        @touchend="midmovee">

        <!-- 推荐组件 -->
        <div class="page">
          <div class="homerecommend">
            <person></person>
            <personmusic></personmusic>
            <recommendList @share="showshare"></recommendList>
          </div>
        </div>

        <!-- 歌手组件 -->
        <div class="page">
          <div class="authors">
            <authors></authors>
          </div>
        </div>

        <!-- 视频组件 -->
        <div class="page">
          <div class="localmusic">
            <localmusic></localmusic>
          </div>
        </div>
      </div>

    </div>


  </div>
</template>
<style src='../css/home.css'></style>
<script>

  // 组件
  import top from "../components/music/top.vue"
  import person from "../components/music/person.vue"
  import recommendList from "../components/music/recommendMusicList.vue"
  import personmusic from "../components/music/personmusic.vue"
  import authors from "../components/music/authors.vue"
  import localmusic from "../components/music/localmusic.vue"


  export default{

    // 数据
    data(){
      return{
        listindex: 1,
        startpointX: 0,
        startpointY: 0,
        startline: 0,
        touchdistance: 0,
        touchdistanceY: 0,
        moveing:0,
        allow:0
      }
    },

    // 组件
    components:{
        'top': top,
        "person":person,
        'personmusic':personmusic,
        "recommendList": recommendList,
        "authors": authors,
        "localmusic": localmusic
    },

    // 方法
    methods:{

      //点击开始记录滑动位置
      midmoves(e){
        this.startpointX = event.touches[0].clientX                                   //开始时的 X 点
        this.startpointY = event.touches[0].clientY                                   //开始时的  点
        let el = document.getElementsByClassName("homemiddle")[0].style.transform
        el = ( el.length == 0 ) ? "(0)" : el
        this.startline = parseInt(el.split("(")[1])                                   //记录开始时的左滑距离
      },

      //滑动
      midmoveing(e){
        this.distance = event.touches[0].clientX - this.startpointX     //水平滑动距离
        let disY = event.touches[0].clientY - this.startpointY          //垂直滑动距离

        if( this.moveing == 0 && Math.abs(this.distance) > Math.abs(disY) ){this.allow = 1}
        this.moveing = 1

        if( this.allow == 1 ){
          let dis = this.startline + this.distance
          let el = document.getElementsByClassName("homemiddle")[0].style
          let totlewidth = -window.outerWidth*2
          if( totlewidth < dis && dis < 0 ){
            el.webkitTransform = "translateX("+dis+"px)"
          }
        }else{
          this.distance = 0
        }
      },

      //滑动结束，判断进入的页面。 并且改变头部的选择状态
      midmovee(){
        this.moveing = 0
        this.allow = 0
        let totlewidth = window.outerWidth
        if( this.distance < -80 ){
          if( this.startline > -totlewidth*2 ){
            let x = this.startline - totlewidth
            this.changed(x,totlewidth)
          }
        }else if( this.distance > 80 ){
          if( this.startline < 0 ){
            let x = this.startline + totlewidth
            this.changed(x,totlewidth)
          }
        }else{
          let el = document.getElementsByClassName("homemiddle")[0].style
          el.webkitTransform = "translateX("+this.startline+"px)"
        }
      },

      //滑动结束 修改左滑 距离 ，并且改变头部样式
      changed(x,y){
        let el = document.getElementsByClassName("homemiddle")[0].style
        el.transitionDuration = "200ms"
        el.webkitTransform = "translateX("+x+"px)"
        this.listindex = -(x/y)+1
        this.distance = 0
        setTimeout(()=>{
            el.transitionDuration = "0ms"
        },200)
      },

      //头部点击传值，执行 自动 滑动操作
      autochange(data){
        if( this.listindex != data ){
          this.listindex = data               //改变该组件 向 头部组件 传值
          let y = window.outerWidth           //屏幕宽度
          let el = document.getElementsByClassName("homemiddle")[0].style
          let dis = -(this.listindex - 1) * y
          el.transitionDuration = "200ms"
          el.webkitTransform = "translateX("+dis+"px)"
          setTimeout(()=>{
              el.transitionDuration = "0ms"
          },200)
        }
      },

      //显示分享页面
      showshare(data){
        this.$emit("share",data)
      }

    }
  }

</script>



