<template>

  <div id="rightset">

    <div @click.stop="disrightset" class="rightsetleft"></div>
    <div class="rightsetright">

      <div class="code">
        <div><img src="../../assets/camera.png">扫一扫</div>
        <div><img src="../../assets/code.png" @click="showmycode">我的二维码</div>
        <div><img src="../../assets/bluefriend.png">我的好友</div>
        <div><img src="../../assets/car.png">驾驶模式</div>
      </div>

      <div class="others">
        <div><img src="../../assets/message.png">消息中心<span>1</span><span></span></div>
        <div><img src="../../assets/VIP.png">会员中心<span>1</span><span></span></div>
        <div><img src="../../assets/card.png">私人云盘<span>6</span><span></span></div>
        <div><img src="../../assets/close.png">定时关闭<span>1</span><span></span></div>
        <div><img src="../../assets/shop.png">商城<span>1</span><span></span></div>
        <div><img src="../../assets/headphone.png">听歌识曲<span>1</span><span></span></div>
        <div><img src="../../assets/music.png">音效<span>1</span><span></span></div>
        <div><img src="../../assets/skin.png">皮肤中心<span>1</span><span></span><div>更换皮肤</div></div>
      </div>

      <div class="rightbottom">
        <div><img src="../../assets/quit.png">退出登录</div>
        <div><img src="../../assets/close.png">关闭</div>
      </div>


    </div>

    <div v-if="showcode" class="mycodepage">
      <div class="mycode">
        <div id="mycode"></div>
        <div @click="closecode">完成</div>
      </div>
    </div>

  </div>

</template>


<script>

  export default{
    data(){
      return{
        showcode:false
      }
    },
    mounted() {
      this.touch()
    },
    methods:{
      disrightset(){
        document.getElementById("rightset").style.display = "none"
      },
      //添加点击效果
      touch(){

        //others列表的点击效果
        let other = document.getElementsByClassName("others")[0].children
        let others = [].slice.call(other)
        others.forEach((item)=>{
          item.addEventListener("touchstart",function(){
            item.style.backgroundColor = "#f1f1f1"
          })
          item.addEventListener("touchend",function(){
            item.style.backgroundColor = "transparent"
          })
        })
        //底部退出按钮的点击效果
        let bottom = document.getElementsByClassName("rightbottom")[0].children
        let bottoms = [].slice.call(bottom)
        bottoms.forEach((item)=>{
          item.addEventListener("touchstart",function(){
            item.style.backgroundColor = "#f1f1f1"
          })
          item.addEventListener("touchend",function(){
            item.style.backgroundColor = "transparent"
          })
        })

      },


      //二维码
      showmycode(){
        this.showcode = true
        let url = this.$url + "#/alt012577"
        setTimeout(()=>{
          $("#mycode").qrcode({
            width:190,
            height:190,
            text:url
          },10)
        })

      },
      
      //关闭二维码
      closecode(){
        $("#mycode").html("")
        this.showcode = false
      }


    }
  }

</script>

<style>

  #rightset{
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 8;
    top:0;
    display: none;
  }
  #rightset .rightsetleft{
    width: 25%;
    height: 100%;
  }
  #rightset .rightsetright{
    width: 75%;
    height: 100%;
    background-color: #fafafa;
    position: relative;
    animation: showrightset 300ms;
    -webkit-box-shadow: 0 0 15px #ddd;
  }
  @-webkit-keyframes showrightset{
    from{transform: translateX(70vw);}
    to{transform: translateX(0vw)}
  }
  #rightset .rightsetright .rightbottom{
    position: absolute;
    bottom: 0;
    display: flex;
    width: 100%;
    height: 50px;
    font-size: 13px;
    background-color: #fff;
  }
  #rightset .rightsetright .rightbottom div{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #rightset .rightsetright .rightbottom img{
    width: 20px;
    height: 20px;
    margin-right: 10px;
    opacity: 0.8;
  }
  #rightset .rightsetright .code{
    display: flex;
    width: 100%;
    margin-top: 50px;
  }
  #rightset .rightsetright .code div{
    width: 25%;
    text-align: center;
    font-size: 10px;
  }
  #rightset .rightsetright .code img{
    display: block;
    width: 30px;
    height: 30px;
    margin: 0 auto 10px;
    background-color: #fff;
    border-radius: 100%;
    padding: 10px;
  }
  #rightset .rightsetright .others{
    width: 100%;
    margin-top: 3mm0px;
  }
  #rightset .rightsetright .others>div>img{
    width: 20px;
    height: 20px;
    margin: 0 5% 0 8%;
  }
  #rightset .rightsetright .others>div{
    display: flex;
    align-items: center;
    width: 100%;
    height: 48px;
    position: relative;
  }
  #rightset .rightsetright .others span:nth-of-type(1){
    background-color: red;
    color: #fff;
    min-width: 12px;
    height: 12px;
    font-size: 10px;
    text-align: center;
    border-radius: 6px;
    margin-left: 8px;
  }
  #rightset .rightsetright .others span:nth-of-type(2){
    width: 7px;
    height: 7px;
    background-color: red;
    border-radius: 100%;
    margin-left: 8px;
  }
  #rightset .rightsetright .others>div>div{
    position: absolute;
    right: 20px;
    color: #aaa;
    font-size: 14px;
  }
  #rightset .mycodepage{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  #rightset .mycodepage .mycode{
      background-color: #fff;
      width: 200px;
      height: 250px;
      padding:8px;
      text-align: center;
  }
  #rightset .mycodepage .mycode div:first-child{
    width: 190px;
    height: 190px;
    margin: 5px;
  }
  #rightset .mycodepage .mycode div:last-child{
    height: 50px;
    line-height: 50px;
    border-top: 0.5px solid #f1f1f1;
  }

</style>



