<template>
  <div id="backgammon">

      <!-- 标题 -->
      <div><img>五子棋</div>

      <div class="back">
        <div class="item" @click="parent(index)" v-for="(item,index) in 10" :key="index">
          <div @click="change(e,index)" class="table" v-for="(table,index) in 10" :key="index"></div>
        </div>
      </div>

      <div class="backImg">
        <div class="item" v-for="(item,index) in 9" :key="index">
          <div class="table" v-for="(table,index) in 9" :key="index"></div>
        </div>
      </div>


  </div>
</template>

<script>

  export default{
    data(){
      return{
          people:1,
          list:[],
          x:0,
          y:0
      }
    },
    mounted() {
      this.createlist()
    },
    methods:{

      //横排 序列号
      parent(index){
        this.x = index
      },

      //点击创建棋子
      change(e,index){
        this.y = index
        if( event.target.style.backgroundColor == "" ){
          let color = (this.people == 1) ? "red" : "black"
          event.target.style.backgroundColor = color
          this.people = ( this.people == 1 ) ? 2 : 1
        }
        let _this = this
        setTimeout(()=>{
          _this.list[_this.x][_this.y] = _this.people
          _this.getwinner(_this.people)
        },100)
      },

      //创建二维数组 10 x 10
      createlist(){
        for(let a=0;a<10;a++){
          this.list[a] = []
          for(let b=0;b<10;b++){
            this.list[a][b] = []
          }
        }
      },

      //判断棋局情况
      getwinner(x){
        let winner = x
        if(this.list[0][0] == winner){
          if(this.list[0][1] == winner){
            if(this.list[0][2] == winner){
              if(this.list[0][3] == winner){
                if(this.list[0][4] == winner){
                }
              }
            }
          }
        }
      }


    }
  }

</script>

<style>
  #backgammon .back{
    width: 100vw;
    height: 100vw;
    position: absolute;
    top: 0;
    z-index: 5;
  }
  #backgammon .back .item{
    width: 100%;
    height: 10vw;
    display: flex;
  }
  #backgammon .back .item .table{
    width: 7vw;
    height: 7vw;
    border-radius: 100%;
    margin: 1.5vw;
  }
  #backgammon .backImg{
    width: 90vw;
    height: 90vw;
    margin:  5vw;
    position: absolute;
    top:0;
    z-index: 0;
  }
  #backgammon .backImg .item{
    width: 100%;
    height: 10vw;
    display: flex;
    border-top: 0.5px solid;
  }
  #backgammon .backImg .item:last-child{
    border-bottom: 0.5px solid;
  }
  #backgammon .backImg .item .table{
    width: 10vw;
    height: 100%;
    border-left: 0.5px solid;
  }
  #backgammon .backImg .item .table:last-child{
    border-right: 0.5px solid;
  }
</style>



