<template>
  <div class="playbottom">
      <img class="playbottomImg" :src="autoImg">
      <img class="playbottomother" src="../../assets/bottom.png">

      <!-- 播放按钮 -->
      <img
        @touchstart="initnumber"
        @touchmove="showplaypage"
        @click="changePlay()"
        v-show="!this.$root.playing"
        class="stoping"
        src="../../assets/playPagePlay.png">
      <!-- 暂停按钮 -->
      <img
        @touchstart="initnumber"
        @touchmove="showplaypage"
        @click="changePlay()"
        v-show="this.$root.playing"
        class="playing"
        src="../../assets/playPageStop.png">

      <div @click="touch(1)">
        <img :src="choosedImg(1)">
        <span :class="choosed(1)">视频</span>
      </div>

      <div @click="touch(2)">
        <img :src="choosedImg(2)">
        <span :class="choosed(2)">音乐</span>
      </div>

      <div></div>

      <div @click="touch(3)">
        <img :src="choosedImg(3)">
        <span :class="choosed(3)">歌单</span>
      </div>

      <div @click="touch(4)">
        <img :src="choosedImg(4)">
        <span :class="choosed(4)">我的</span>
      </div>

      <playingpage id="playingpage"></playingpage>

  </div>
</template>

<script>

  import autoImg from "../../assets/personImg.jpg"

  import playingpage from "./playingpage.vue"

  import mineblue from "@/assets/mine_blue.png"
  import minegray from "@/assets/mine_gray.png"
  import messageblue from "@/assets/messageblue.png"
  import messagegray from "@/assets/messagegray.png"
  import musicblue from "@/assets/musicblue.png"
  import musicgray from "@/assets/musicgray.png"
  import localblue from "@/assets/localblue.png"
  import localgray from "@/assets/localgray.png"


  export default{
    props:["isplaying"],
    data(){
      return{
        chooseditem: 2,
        touchshowstart: 0,
        autoImg: autoImg,
      }
    },

    components:{
      "playingpage": playingpage
    },
    mounted() {
    },



    methods:{

      //点击 播放  或者暂停
      changePlay(){
        this.$root.playing = !this.$root.playing
        let anim = document.getElementsByClassName("playbottomImg")[0].style
        anim.animationPlayState = this.$root.playing ? "running" : "paused"     //旋转的暂停与继续
        if( this.$root.playing == true ){this.$audio.play()}                    //播放的暂停与继续
        else{this.$audio.pause()}
      },

      //上滑显示播放页面
      initnumber(e){
          this.touchshowstart = event.touches[0].clientY
      },
      showplaypage(e){
        let x = event.touches[0].clientY - this.touchshowstart
        let y = document.getElementById("playingpage")
        if( x < -100 ){
          if( y != null ){
            y.id = "playingpageshow"
          }else{
            y = document.getElementById("playingpagenone")
            if( y != null ){
              y.id = "playingpageshow"
            }
          }
        }

      },

      choosed(x){
          if( x == this.chooseditem ){
            return "choosed"
          }
      },
      choosedImg(x){
        if(x == 1){
          if(this.chooseditem == 1){return localblue}
          else{return localgray}}
        else if(x == 2){
          if(this.chooseditem == 2){return musicblue}
          else{return musicgray}}
        else if(x == 3){
          if(this.chooseditem == 3){return messageblue}
          else{return messagegray}}
        else if(x == 4){
          if(this.chooseditem == 4){return mineblue}
          else{return minegray}}
      },
      touch(x){
        this.chooseditem = x
        this.$emit("showorno",this.chooseditem)
      }
    }
  }

</script>

<style>
  .playbottom{
    position: fixed;
    width: 100%;
    background-color: #fff;
    height: 55px;
    bottom: 0;
    border-radius: 8px 8px 0 0;
    display: flex;
    z-index: 15;
    animation: showbottom 200ms;

  }
  @-webkit-keyframes showbottom{
    from{transform: translateY(55px);}
    to{transform: translateY(0px);}
  }
  .playbottom div{
    font-size: 0.7rem;
    width: 20%;
    text-align: center;
    color: dimgray;
  }
  .playbottom>div>img{
    width: 30px;
    display: block;
    margin: 5px auto 2px;
  }
  .choosed{
    color: #1296db;
  }
  .playbottomImg{
    width: 3.12rem;
    height: 3.12rem;
    border-radius: 100%;
    position: fixed;
    bottom: 0.625rem;
    margin-left: calc(50% - 1.4rem);
    animation: playing 10s linear infinite;
    animation-play-state: paused;
  }
  @-webkit-keyframes playing{
    from{transform: rotate(0);}
    to{transform: rotate(360deg);}
  }
  .playbottomother{
    position: fixed;
    bottom: 55px;
    z-index: -1;
    width: 7rem;
    height: 1rem;
    left: 50%;
    transform: translateX(-50%);
  }
  .stoping,.playing{
    width: 1.375rem;
    height: 1.5625rem;
    opacity: 0.3;
    position: fixed;
    bottom: 1.4rem;
    left: calc(50% + 3px);
    transform: translateX(-50%);
  }
  #playingpage{
    position: fixed;
    height: 100vh;
    background-color: #dfdcd7;
    top:0;
    transform: translateY(100vh);
  }
  #playingpageshow{
    position: fixed;
    height: 100vh;
    top:0;
    background-color: #dfdcd7;
    animation: playingpageshow 300ms;
  }
  @-webkit-keyframes playingpageshow{
    from{transform: translateY(20vh);opacity: 0;}
    to{transform: translateY(0vh);opacity: 1;}
  }
  #playingpagenone{
    position: fixed;
    height: 100vh;
    background-color: #dfdcd7;
    top: 0;
    left: 0;
    transform: translateY(100vh);
    animation: playingpagenone 300ms;
  }
  @-webkit-keyframes playingpagenone{
    0%{transform: translateY(0vh);opacity: 1;}
    99%{transform: translateY(20vh);opacity: 0;}
    100%{transform: translateY(100vh);opacity: 0;}
  }
</style>



