<template>

  <div>
    <!-- 本地音乐 -->
    <local v-if="showpage[0]" v-show="changepage(1)"></local>
    <!-- 音乐列表 -->
    <music @share="showshare" v-if="showpage[1]" v-show="changepage(2)"></music>
    <!-- 歌单 -->
    <musiclist v-if="showpage[2]" v-show="changepage(3)"></musiclist>
    <!-- 我的页面 -->
    <mine v-if="showpage[3]" v-show="changepage(4)"></mine>
    <!-- 底部按钮 -->
    <playbottom
      @showorno="changethepage"
      :isplaying="isplaying"
      ></playbottom>
    <!-- 右边设置 -->
    <rightset></rightset>
    <!-- 分享 -->
    <share @disshare="disshare" :item="shareitem" v-if="share"></share>

  </div>

</template>

<script>
  import music from "./Music.vue"
  import local from "./local.vue"
  import musiclist from "./musiclist.vue"
  import mine from "./mine.vue"
  import playbottom from "../components/bottom/playbottom.vue"
  import rightset from "../components/rightset/rightset.vue"
  import share from "../components/share/share.vue"

  export default{
    data(){
      return{
        page:2,//初次 页面
        isplaying: false,//进入不进行播放
        playingpage: 0,
        //歌曲信息
        music: {
          url:"",
          name: "",
          author: ""
        },
        showpage:["","true","",""],
        themusic: 0,//创造音乐
        share:false,
        shareitem:""
      }
    },

    // 组件
    components:{
      "local":local,
      "music":music,
      "musiclist":musiclist,
      "mine":mine,
      "playbottom":playbottom,
      "rightset":rightset,
      "share":share
    },


    mounted() {
    },


    methods:{

      //绑定函数  切换页面（本地，音乐，歌单，我的）
      changethepage(data){
        this.page = data
        this.showpage[this.page-1] = true
      },

      //动态响应 页面的 显示与隐藏
      changepage(x){
          if( x == this.page ){return true}
          else{return false}},

      //显示分享
      showshare(data){
        this.shareitem = data
        this.share = true
      },

      //关闭分享
      disshare(){
        this.share = false
      }

    }

  }
</script>



