//滑动开始
function start(){
    let style = document.getElementsByClassName("videolist")[0].style.transform
    style = ( style == "" ) ? "translateX(0vw)" : style
    let start = parseInt(style.split("(")[1])
    return start
}

//滑动结束
function move(startline,distanceX){
    
}

//滑动结束
function end(startline,distanceX){
    let width = window.outerWidth
    let el = document.getElementsByClassName("videolist")[0].style
    el.transitionDuration = "300ms"
    if( Math.abs(distanceX) > 80 ){
      if( distanceX < -80 ){
        startline = startline - width
        startline = ( startline < -width*8 ) ? -width*8 : startline
        el.transform = "translateX("+startline+"px)"
      }else if( distanceX > 80 ){
        startline = startline + width
        startline = ( startline > 0 ) ? 0 : startline
        el.transform = "translateX("+startline+"px)"
      }
    }else{
      el.transform = "translateX("+startline+"px)"
    }
    setTimeout(()=>{
      el.transitionDuration = "0ms"
    },10)
    return startline
}

//点击滑动
function touchsort(x,y){
  let width = window.outerWidth
  let el = document.getElementsByClassName("videolist")[0].style
  let dis = -width*x
  el.opacity = 0
  el.transform = "translateX("+dis+"px)"
  setTimeout(()=>{
    el.transitionDuration = "300ms"
    el.opacity = 1
  },10)
  setTimeout(()=>{
    el.transitionDuration = "0ms"
  },310)
}

export{start,move,end,touchsort}



