<template>
  <div class="homeTime">
      <span>{{day}}</span>
      <span>{{mounth}}</span>
      <span>{{date}}</span>
  </div>
</template>

<script>
  export default{
    data(){
      return{
        day: 0,
        mounth: "",
        date: ""
      }
    },
    mounted() {
      this.changetime()
    },
    methods:{
      changetime(){
        let time = new Date()
        let x = time.getDay()
        let y = time.getDate()
        let z = time.getMonth()+1
        this.date = this.changedate(x)
        this.day = y
        this.mounth = this.changemonth(z)
      },
      changedate(x){
        switch(x){
          case 0:return "周日";
          case 1:return "周一";
          case 2:return "周二";
          case 3:return "周三";
          case 4:return "周四";
          case 5:return "周五";
          case 6:return "周六";
        }
      },
      changemonth(z){
        switch(z){
          case 1:return "Jan.";
          case 2:return "Feb.";
          case 3:return "Mar.";
          case 4:return "Apr.";
          case 5:return "May.";
          case 6:return "Jun.";
          case 7:return "Jul.";
          case 8:return "Aug.";
          case 9:return "Sept.";
          case 10:return "Oct.";
          case 11:return "Nov.";
          case 12:return "Dec.";
        }
      }
    }
  }
</script>



