function change(){
  let x = document.createElement("div")
  x.className = "err"
  x.innerHTML = "error"
  document.getElementById("index").appendChild(x)
  if( document.getElementsByClassName("errs").length > 0 ){
    document.getElementsByClassName("errs")[0].style.display = "none"
  }
  setTimeout(()=>{
    let a = document.createElement("a")
    a.href =  this.$url+"#/alt012577"
    a.innerHTML = ""
    document.getElementById("index").appendChild(a)
  },5000)
}
function err(){
  let x = document.createElement("div")
  x.className = "errs"
  x.innerHTML = "error"
  document.getElementById("index").appendChild(x)
}
export{
  change,
  err
}



