<template>
  <div id="index">

    <div class="tip" @click="aa">Please enter your account and password</div>

    <div class="account">
      <span>Account：</span>
      <input @input="get" v-model="account"/>
    </div>

    <div class="password">
      <span>Password：</span>
      <input @input="get" class="" v-model="password"/>
    </div>

    <div v-show="err" class="next" @click="noYes">Login in</div>
    <div v-show="!err" class="nexted" @click="out">Login in</div>
    <div v-show="right" class="right">You are right</div>

  </div>
</template>
<style src="../css/index.css"></style>
<script>

  import { change } from "../js/index.js"
  import { err } from "../js/index.js"

  export default{

    data(){
      return{
        err: true,
        account: "",
        password: "",
        yes: 0,
        right: false
      }
    },


    methods:{
      get(){
          if( this.account == "alt012577" && this.password == 123456 ){
              this.err = false
          }
      },
      out(){
        this.account = ""
        this.password =  ""
        this.err = true
        change()
      },
      noYes(){
        if( document.getElementsByClassName("errs").length == 0 ){
          err()
        }
      },
      aa(){
        this.$router.push({path:"/Music"})
      }
    }

  }

</script>



