<template>
  <div id="share">

      <div class="top" @click="disshare"></div>

      <div v-if="showcopyed" class="copyed">
          <div>已复制</div>
          <div v-text="text"></div>
          <div class="discopy"><div @click="discopy">取消</div><div @click="discopy">去粘贴</div></div>
      </div>

      <div v-if="showwechat" class="wechat">
        <div>
          <div id="musicwechatshare"></div>
          <div>
            <div><img :onerror="errorimg" :src="msg.imgUrl"></div>
            <div><div>{{msg.author}}</div><div>{{msg.name}}</div></div>
          </div>
          <div><div @click="closewechat">取消</div><div @click="download">保存</div></div>
        </div>
      </div>

      <div class="apps">

        <div>分享到</div>

        <div>
          <div>
            <div @click="showweixin"><img src="../../assets/we-chat.png">微信</div>
            <div><img src="../../assets/weibo.png">微博</div>
            <div @click="showweixin"><img src="../../assets/peng_you_quan.png">朋友圈</div>
            <div><img src="../../assets/qq.png">QQ好友</div>
            <div><img src="../../assets/QQkongjian.png">QQ空间</div>
            <div><img src="../../assets/share.png">转发</div>
          </div>
        </div>

        <div>
          <div>
            <div @click="downloadmusic"><div><img src="../../assets/download.png"></div>保存本地</div>
            <div><div><img src="../../assets/collection.png"></div>收藏</div>
            <div @click="showcopy"><div><img src="../../assets/copy.png"></div>复制连接</div>
            <div><div><img src="../../assets/codegray.png"></div>二维码</div>
            <div><div><img src="../../assets/ringgray.png"></div>设置闹铃</div>
          </div>
        </div>

        <div @click="disshare">取<span></span>消</div>

      </div>
  </div>
</template>

<script>

	import err from "../../assets/error.png"

    export default{
      props:["item"],
      data(){
        return{
          errorimg: 'this.src="'+err+'"',
          showcopyed:false,
          text:"",
          showwechat:false,
          msg:{}
        }
      },
      mounted() {
        this.text = this.item.url
        this.msg = this.item
      },
      methods:{
        showcopy(){
          this.showcopyed = true
          let input = document.createElement("input")
          document.body.appendChild(input)
          input.style.opacity=0
          input.value = this.text
          input.select()
          document.execCommand("Copy")
          input.remove()
          this.touchcolor()
        },
        discopy(e){
          this.showcopyed = false
        },
        touchcolor(){
          setTimeout(()=>{
            let other = document.getElementsByClassName("discopy")[0].children
            let others = [].slice.call(other)
            others.forEach((item)=>{
              item.addEventListener("touchstart",function(){
                item.style.backgroundColor = "#f3f3f3"
              })
              item.addEventListener("touchend",function(){
                item.style.backgroundColor = "transparent"
              })
            })
          },200)

        },
        disshare(){
          this.$emit('disshare')
        },

        //显示微信分享
        showweixin(){
          this.showwechat = true
          setTimeout(()=>{
            $("#musicwechatshare").qrcode({
              width:200,
              height:200,
              text:this.msg.url
            },10)
          })
        },
        //关闭微信分享
        closewechat(){
          $("#musicwechatshare").html("")
          this.showwechat = false
        },

        //保存图片
        download(){
          html2canvas(document.getElementsByClassName("wechat")[0].children[0],{
            useCORS: true,
            allowTaint: true,
          }).then(canvas => {
            // document.getElementsByClassName("screenshot")[0].appendChild(canvas)
            var image = new Image();//创建新图片
            image.src = canvas.toDataURL("image/png",);   //设置图片路径
            var url = image.src;                            // 获取图片地址
            var a = document.createElement('a');          // 创建一个a节点插入的document
            var event = new MouseEvent('click')           // 模拟鼠标click点击事件
            a.download = 'screenshot.png'                  // 设置a节点的download属性值
            a.href = url;                                 // 将图片的src赋值给a节点的href
            a.dispatchEvent(event)
          });
        },

        //保存歌曲
        downloadmusic(){
          let a = document.createElement("a")
          a.href = this.msg.url
          let ev = new MouseEvent("click")
          a.dispatchEvent(ev)
        }

      }
    }

</script>

<style src="../../css/share.css"></style>



