<template>
  <div id="novideo">
    <img @click="show" :class="image" src="../../assets/nothingtip.png">
    <div>空空如也</div>
  </div>
</template>

<script>
  export default{

    data(){
      return{
        image:"image"
      }
    },

    methods:{
        show(){
            this.image = "imageX"
            let _this = this
            setTimeout(()=>{
              _this.image = "image"
            },500)
        }
    }
  }
</script>

<style>
  #novideo .image{
    width: 200px;
    margin: 100px auto 0;
    display: block;
  }
  #novideo .imageX{
    width: 200px;
    margin: 100px auto 0;
    display: block;
    animation: bigsmall 500ms;
  }
  @-webkit-keyframes bigsmall{
    0%{width: 180px;}
    70%{width: 300px;}
    100%{width: 200px;}
  }
  #novideo div{
    margin-top: 50px;
    width: 100%;
    text-align: center;
    color: gray;
    letter-spacing: 10px;
  }
</style>



