<template>

    <div id="mine">

      <div class="topback">
        <img class="backimg" src="../assets/images/mineback.jpg">
        <img @click="showrightset" class="rightset" src="../assets/setup.png">
        <top></top>
        <div class="somthing">
          <div><img src="../assets/minemessage.png"><div>我的消息</div></div>
          <div><img src="../assets/minefriend.png"><div>我的好友</div></div>
          <div><img src="../assets/minehome.png"><div>个人主页</div></div>
          <div><img src="../assets/minetheme.png"><div>个性装扮</div></div>
        </div>
      </div>

      <div class="minemsg" @touchstart="downstart" @touchmove="down" @touchend="downend">

        <div class="mylist">
            <div>我的音乐</div>
            <div>
              <div>
                <img class="backimg" src="../assets/images/backimgpiao.jpg">
                <img class="icon" src="../assets/likeheart.png">
                <div>我喜欢的音乐</div>
              </div>
              <div>
                <img class="backimgs" src="../assets/images/backimgother.jpg">
                <img class="icon" src="../assets/localgray.png">
                <div>其他</div>
              </div>
            </div>
        </div>

        <div class="personaldata">
          <div>基本资料</div>
          <div>等级 15（2020年1月注册）</div>
          <div>地区：浙江省 杭州市</div>
          <div>社交账号：<img src="../assets/weibo.png"></div>
        </div>

      </div>

    </div>

</template>

<style src="../css/mine.css"></style>

<script>

  import top from "../components/music/person.vue"

  export default{
    data(){
      return{
        startpoint: 0
      }
    },
    components:{
      "top":top
    },
    methods:{
      //向下滑动动画效果
      downstart(e){
        this.startpoint = event.touches[0].clientY
      },
      down(e){
        let distance = event.touches[0].clientY - this.startpoint
        if( distance < 150 && distance > 0 ){
          let y = document.getElementsByClassName("minemsg")[0].style
          y.transform = "translateY("+distance/2+"px)"
          let z = document.getElementsByClassName("backimg")[0].style
          z.width = ""+(100+distance/4)+"%"
          z.height = ""+(100+distance/4)+"%"
          let a = document.getElementsByClassName("somthing")[0].style
          a.width = ""+(90+distance/20)+"%"
          a.top = ""+(60+distance/10)+"%"
        }
      },
      downend(){
        document.getElementsByClassName("minemsg")[0].style.transform = "translateY(0px)"
        let z = document.getElementsByClassName("backimg")[0].style
        z.transitionDuration = "200ms"
        z.width = "100%"
        z.height = "100%"
        let a = document.getElementsByClassName("somthing")[0].style
        a.transitionDuration = "200ms"
        a.width = "90%"
        a.top = "60%"
        let b = document.getElementsByClassName("minemsg")[0].style
        b.transitionDuration = "200ms"
        setTimeout(()=>{
          z.transitionDuration = "0ms"
          a.transitionDuration = "0ms"
          b.transitionDuration = "0ms"
        })
      },
      //点击设置按钮，显示右边设置页面
      showrightset(){
        document.getElementById("rightset").style.display= "flex"
      }
    }
  }

</script>



