<template>
  <div id="videopage">

    <!-- 标题 -->
    <h3>视频</h3>

    <!-- 类别 -->
    <div class="title">
      <div class="titles">
        <span
          v-for="(item,index) in videosortList"
          :class="fontweigt(index)"
          @click="videopages(index)">{{item}}</span>
      </div>
    </div>


    <!-- 视频列表 -->
    <div class="differentpage">
      <div class="videolist"
        @touchstart="nextchange"
        @touchmove="nextpage"
        @touchend="changeend">
        <div class="pagenumber" v-for="(item,index) in videosortList.length">
          <videolist v-bind:url="urllist[index]" v-if="show[index]" v-bind:page="page"></videolist>
        </div>
      </div>
    </div>


  </div>
</template>

<style src="../css/videopage.css"></style>

<script>

  import videolist from '../components/video/videolist.vue'

  import { start,move,end,touchsort } from '../js/videopage.js'




  export default{

    data(){
      return{
        videosortList:[],
        nothingtip: false,
        page: 0,
        startpointX:0,
        startpointY:0,
        distanceX:0,
        startline:0,
        show:["true","","","","","","","",""],
        urllist:[
          "video.json",
          "shortvideo.json",
          "newvideo.json",
          "mv.json",
          "locationvideo.json",
          "dance.json",
          "chinesestyle.json",
          "varietyshow.json",
          "chinese.json",
        ],
        allow:0,
        moveing:0

      }
    },

    // 组件
    components:{
      "videolist": videolist
    },

    mounted() {
      this.videosorts()
    },

    methods:{
      //获取视频分类
      async videosorts(){
          await axios({
            url: this.$url + "static/videojson/videosort.json"
          })
          .then((data)=>{
            this.videosortList = data.data.list
          })
      },

      //动态加载所点击的页面
      showpages(x){
        if( this.page == x ){return true}
        else{return false}
      },

      // 点击返回所选择的 page
      videopages(x){
        touchsort(x,this.page)
        this.page = x
        this.show[this.page] = true
        document.getElementsByClassName("title")[0].scrollLeft = 80*(x-2)
      },

      //标题字体样式
      fontweigt(x){if( this.page == x ){return "bigfont"}},

      //根据 列表信息返回提示信息
      tips(data){
      },

      //滑动开始
      nextchange(e){
          this.startpointX = event.touches[0].clientX
          this.startpointY = event.touches[0].clientY

          this.startline = start()
      },

      //滑动中
      nextpage(e){
          this.distanceX = event.touches[0].clientX - this.startpointX     //水平滑动距离
          let disY = event.touches[0].clientY - this.startpointY          //垂直滑动距离

          if( this.moveing == 0 && Math.abs(this.distanceX) > Math.abs(disY) ){this.allow = 1}
          this.moveing = 1

          if( this.allow == 1 ){
            let dis = this.startline + this.distanceX
            let el = document.getElementsByClassName("videolist")[0].style
            let totlewidth = -window.outerWidth*2
            if( totlewidth < dis && dis < 0 ){
              el.webkitTransform = "translateX("+dis+"px)"
            }
          }else{
            this.distanceX = 0
          }
      },
      //滑动结束
      changeend(){
        console.log(this.distanceX)
        this.moveing = this.allow = 0
        let width = window.outerWidth
        this.startline = end(this.startline,this.distanceX)
        this.distanceX = 0
        this.page = Math.abs(this.startline/width)
        this.show[this.page] = true
        document.getElementsByClassName("title")[0].scrollLeft = 80*(this.page-2)
      }

    }
  }
</script>



