<template>
  <div id="tvplay">

    <div>
      <div>{{item.name}}</div>
      <video v-if="video" id="myVideo" autoplay controls preload="auto">
          <source :src="url"  type="application/x-mpegURL">
      </video>
    </div>

    <div>第<span v-text="juji"></span>集</div>

    <div>
      <div  :class="choosed(index)"
            v-for="(item,index) in this.list.length"
            @click="changeurl(item)"
            :key="index">{{item}}</div>
    </div>

    <div @click="$router.back(-1)">返回</div>

  </div>
</template>

<script>
  export default{
    data(){
      return{
        url:'',
        item:[],
        list:[],
        video:false,
        juji:1
      }
    },
    created() {
      this.item = this.$route.query.item
      this.init()
    },
    methods:{
      async init(){
        await axios({
          url : this.$url + this.$route.query.item.url
        })
        .then((data)=>{
          this.list = data.data.list
          this.url = this.list[0]
          this.video = true
        })
      },

      changeurl(item){
        this.url = this.list[item-1]
        this.juji = item
        this.video = false
        let _this = this
        setTimeout(()=>{
          _this.video = true
        },10)
      },
      choosed(index){
         if(this.juji-1 == index){return "blue"}
      }
    }
  }
</script>

<style>
  #tvplay{
    width: 100vw;
    height: 100vh;
    background-color: #E1E8FF99;
  }
  #tvplay>div:first-child{
    width: 100%;
    height: 50vh;
    overflow: hidden;
  }
  #tvplay>div:first-child>div{
    height: 100px;
    line-height: 100px;
    font-weight: bolder;
    text-align: center;
    font-size: 1.25rem;
    color: #333;
  }
  #tvplay video{
    width: 100%;
    height: 300px;
    background-color: #E1E8FF99;
  }
  #tvplay>div:nth-of-type(2){
    margin-top: 20px;
    text-align: center;
  }
  #tvplay>div:nth-of-type(3){
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
  }
  #tvplay>div:nth-of-type(3)>div{
    width: 50px;
    height: 30px;
    border-radius: 3px;
    border: 0.5px solid;
    margin: 10px 0 0 20px;
    text-align: center;
    line-height: 30px;
  }
  #tvplay>div:nth-of-type(4){
    border: 0.5px solid;
    font-size: 0.875rem;
    position: absolute;
    bottom: 30px;
    width: 80px;
    height: 35px;
    border-radius: 18px;
    text-align: center;
    line-height: 35px;
    left: calc(50vw - 40px);
    color: gray;
  }
  #tvplay .blue{
    color: #00D5FF;
  }
</style>



