<template>
  <div class="playpage">

    <div class="top">
      <img class="back" @click="closepage" src="../../assets/down.png">
    </div>

    <div class="middle">
      <div :style="isplaying" class="picture">
        <img class="backimg" src="../../assets/musicback.png">
        <img src="../../assets/personImg.jpg">
      </div>
    </div>

    <div class="line">
      <div v-text="this.$root.numbernow"></div>
      <div class="lineback">
        <div class="linered"></div>
        <div class="lineball"></div>
      </div>
      <div v-text="this.$root.numberend"></div>
    </div>

    <div class="playbutton">
      <img src="../../assets/playLeft.png">
      <img @click="playmusic(0)" v-show="!this.$root.playing" src="../../assets/playRight.png">
      <img @click="playmusic(1)" v-show="this.$root.playing"  src="../../assets/playStop.png">
      <img src="../../assets/playRight.png">
    </div>

  </div>
</template>

<script>


  export default{
    data(){
      return{
        isplaying:{animationPlayState: "paused"},
      }
    },

    mounted() {
      this.playing()
      this.getnowtime()
    },

    methods:{

      //关闭播放页面
      closepage(){
        let x = document.getElementById("playingpageshow")
        x.id = "playingpagenone"
      },

      //图片的选装与停止
      playing(){
          if( this.$root.playing == true ){
            this.isplaying = {animationPlayState: "running"}
          }else{
            this.isplaying = {animationPlayState: "paused"}
          }
      },

      //播放与停止
      playmusic(x){
          this.$root.playing = ( x == 1 ) ? false : true
          let y = this.$root.playing ? this.$audio.play() : this.$audio.pause()
      },

      //获取此时时间
      getnowtime(){
            setInterval(()=>{
              let linered = document.getElementsByClassName("linered")[0].style
              let lineball = document.getElementsByClassName("lineball")[0].style
              let x = parseInt((this.$audio.currentTime/this.$audio.duration)*100) + "%"
              linered.width = x
              lineball.left = x
              this.$root.numbernow = this.changenumber(this.$audio.currentTime)
            },1000)
      },

      //更改数字样式
      changenumber(x){
        let number = parseInt(x)
        let first = parseInt(number/60).toString().padStart(2,"0")
        let second = parseInt(number%60).toString().padStart(2,"0")
        let y = first + " : " + second
        return y
      }
    }
  }
</script>

<style>
  .playbottom .playpage{
    width: 100vw;
  }
  .playbottom .playpage .top{
    padding-top: 0.625rem;
  }
  .playbottom .playpage .back{
      width: 1.25rem;
      height: 1.25rem;
      padding: 0.625rem;
  }
  .playbottom .playpage .middle{
    width: 100%;
    margin-top: 60px;
    position: relative;
  }
  .playbottom .playpage .middle .picture{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 280px;
    height: 280px;
    border-radius: 100%;
    overflow: hidden;
    border: 4px solid #eee;
    margin: 0 auto;
    position: relative;
    background-color: #000000;
    -webkit-box-shadow: 0 0 15px #ccc;
    animation: rotating 15s infinite linear ;
  }
  @-webkit-keyframes rotating{
    from{transform: rotate(0);}
    to{transform: rotate(360deg);}
  }
  .playbottom .playpage .middle img{
    width: 220px;
    height: 220px;
    border-radius: 100%;
  }
  .playbottom .playpage .middle .backimg{
    width: 100%;
    height: 100%;
    border-radius: 0;
    position: absolute;
    z-index: -1;
    opacity: 0.1;
  }
  .playbottom .playpage .line{
    display: flex;
    width: 100%;
    align-items: center;
    position: absolute;
    bottom: 200px;
  }
  .playbottom .playpage .line>div:first-child,
  .playbottom .playpage .line>div:last-child{
    width: 25%;
    text-align: center;
  }
  .playbottom .playpage .line .lineback{
    width: 50%;
    height: 2px;
    background-color: #ccc;
    border-radius: 2px;
    position: relative;
  }
  .playbottom .playpage .line .lineback .linered{
    height: 100%;
    background-color: red;
    border-radius: 2px;
  }
  .playbottom .playpage .line .lineback .lineball{
    height: 20px;
    width: 20px;
    border-radius: 100%;
    background-color: #fff;
    position: absolute;
    top:-10px;
    transform: translateX(-10px);
  }
  .playbottom .playpage .playbutton{
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 60px;
  }
  .playbottom .playpage .playbutton img:nth-of-type(2),
  .playbottom .playpage .playbutton img:nth-of-type(3){
    width: 38px;
    height: 38px;
  }
  .playbottom .playpage .playbutton img:nth-of-type(1),
  .playbottom .playpage .playbutton img:nth-of-type(4){
    width: 20px;
    height: 20px;
  }
  .playbottom .playpage .playbutton img{
    padding: 20px;
    border-radius: 100%;
    border: 1px solid #f1f1f1;
    margin-left: calc(25vw - 49.5px);
  }
</style>



